@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
.dark-background {
    background-color: rgba(0,0,0,1);
    min-height: 90vh !important;
    padding-top: 40px !important;
}
.align-center {
    text-align: center;
    color: white;
    font-family: 'Montserrat', sans-serif;
    position: relative;
    margin-top: 20px;
    margin-bottom: 40px;
}

.grid {
    background-color: black;
}

.align-center::after {
    background: linear-gradient(to bottom, rgba(56, 6, 238, 0.8), rgba(8, 28, 212, 0.8)) center 1.08em/100% 2px no-repeat;
    content: "";
    height: 100%;
    left: 41%;
    position: absolute;
    top: 40%;
    width: 18%;
    will-change: transform;
    z-index: 1;
}

.width-img {
    opacity: 0.8 !important;
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 ) !important;
    backdrop-filter: blur( 4px ) !important;
    -webkit-backdrop-filter: blur( 4px ) !important;
    border-radius: 10px !important;
}

.about-text {
    color: #b3a7b1;
    font-family: 'Montserrat', sans-serif;
    text-align: left;
}

@media (max-width: 600px) {
    .about-text {
        padding-right: 10vw;
        padding-left: 10vw;
    }
    .img-box {
        min-height: 40vh;
    }
    p {
        line-height: 190%;
    }
    .Box {
        min-height: 80vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .dark-background {
        min-height: 120vh;
    }
}
@media (min-width: 600px) {
    .Box {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 70vh;
    }
    .about-text {
        padding-right: 10vw;
    }
    .img-box {
        min-height: 70vh;
    }
    p {
        line-height: 180%;
    }
}
.white-color {
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 10px;
}
@media (max-width:600px) {
    .dark-background {
        min-height: 96vh !important;
    }
}

